import React, { Component } from 'react';

import './App.css';
import {
  Container, Grid, Paper, makeStyles, Slider, Input, GridList, GridListTile, Button
  , List, ListItem, ListItemIcon, ListItemText, ListItemSecondaryAction
  , IconButton, Checkbox, Radio, RadioGroup, FormControlLabel, InputAdornment, TextField, Typography
} from '@material-ui/core';
import { VolumeUp, VolumeDown, Comment as CommentIcon } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 500,
    backgroundColor: theme.palette.background.paper,
  },
  rooty: {
    padding: theme.spacing(3, 2),
  },
  rootx: {
    width: '200px',
  },
  rootz: {
    width: '300px',
  },
  title: {
    paddingTop: theme.spacing.unit * 3,
    paddingRight: theme.spacing.unit * 3,
    paddingLeft: theme.spacing.unit * 3,
  },
  todoListRoot: {
    padding: theme.spacing.unit * 3,
  },
  todoListContent: {
    maxWidth: 950,
    padding: theme.spacing.unit * 3,
    marginLeft: 'auto',
    marginRight: 'auto',
  },
});

class Test extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: null,
      checked: [0],
      gender: 'male',
      nenrei: 30,
      shinchou: 150,
      taijyu: 60,
    }
  }

  handleSliderChange = (prop, event, newValue) => {
    this.state[prop] = newValue
    this.setState({ ...this.state });
  };

  handleInputChange = (prop, event) => {
    this.state[prop] = event.target.value === '' ? '' : Number(event.target.value)
    this.setState({ ...this.state });
  };

  radioHandleChange(event) {
    this.setState({ ...this.state, radioValue: event.target.value });
  }

  handleBlur = (prop, min, max) => {
    if (this.state[prop] < min) {
      this.state[prop] = min
      this.setState({ ...this.state });
    } else if (this.state[prop] > max) {
      this.state[prop] = max
      this.setState({ ...this.state });
    }
  }

  handleToggle = value => () => {
    const { checked } = this.state
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    this.setState({ ...this.state, checked: newChecked });
  };

  test = value => {
    return value.map(ss => {
      return (
        <FormControlLabel
          value="female"
          control={<Radio color="primary" />}
          label="〜5.5"
          labelPlacement="end"
        />
      )
    })
  }


  render() {
    const contents = [
      , { id: 1, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
      , { id: 2, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
      , { id: 3, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
      , { id: 4, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
      , { id: 5, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
    ]
    const { value, checked, radioValue } = this.state

    return (
      <div className={this.props.classes.todoListRoot}>
        <Paper className={this.props.classes.todoListContent} style={{ display: 'none' }}>
          <Typography variant="h5" className={this.props.classes.title}>結果{this.state['value']}</Typography>  {/* classNameを追加 */}
        </Paper>
        <Paper className={this.props.classes.todoListContent}>
          <Typography variant="h5" className={this.props.classes.title}>最近の更新{this.state['value']}</Typography>  {/* classNameを追加 */}
          <List className={this.props.classes.root}>
            {contents.map(data => {
              const labelId = `checkbox-list-label-${data.type}`;
              data.ctrl.kouho.map(ss => {
                return (
                  <FormControlLabel
                    value="female"
                    control={<Radio color="primary" />}
                    label="〜5.5"
                    labelPlacement="end"
                  />
                )
              })
              let description =
                <RadioGroup aria-label="gender" name="gender2" value={'female'} className={this.props.classes.rootz}>
                  <div>
                  </div>
                </RadioGroup>
              return (
                //          <ListItem key={type} role={undefined} dense button onClick={handleToggle(type)}>
                <ListItem key={data.id} role={undefined} dense >
                  <ListItemIcon>
                    <Checkbox
                      edge="start"
                      checked={checked.indexOf(data.id) !== -1}
                      tabIndex={-1}
                      disableRipple
                      inputProps={{ 'aria-labelledby': labelId }}
                    />
                  </ListItemIcon>
                  <ListItemText id={labelId} primary={`${data.title}`} />
                  {description}
                </ListItem>
              );
            })}
          </List>
        </Paper>
      </div>
    );
  }
}

//export default App;
export default withStyles(styles)(Test)