import React, { Component } from 'react';

import './App.css';
import {
  Container, Grid, Paper, makeStyles, Slider, Input, GridList, GridListTile, Button
  , List, ListItem, ListItemIcon, ListItemText, ListItemSecondaryAction
  , IconButton, Checkbox, Radio, RadioGroup, FormControlLabel, InputAdornment, TextField, Typography
} from '@material-ui/core';
import { VolumeUp, VolumeDown, Comment as CommentIcon } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 500,
    backgroundColor: theme.palette.background.paper,
  },
  rooty: {
    padding: theme.spacing(3, 2),
  },
  rootx: {
    width: '200px',
  },
  rootz: {
    width: '300px',
  },
  title: {
    paddingTop: theme.spacing.unit * 3,
    paddingRight: theme.spacing.unit * 3,
    paddingLeft: theme.spacing.unit * 3,
  },
  todoListRoot: {
    padding: theme.spacing.unit * 3,
  },
  todoListContent: {
    maxWidth: 950,
    padding: theme.spacing.unit * 3,
    marginLeft: 'auto',
    marginRight: 'auto',
  },
});

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: null,
      checked: [0],
      gender: 'male',
      nenrei: 30,
      shinchou: 150,
      taijyu: 60,
      ketsuatsu: '1',
      kouketsuatsu: '1',
      hba1c: '1',
      hdl: '1',
      ldl: '1',
      tounyoubyou: '1',
      tabako: '1',
      sports: '1',
      kekka:false
    }
  }

  onClick = () => {
    this.setState({ ...this.state, kekka: true });
  }

  handleSliderChange = (prop, event, newValue) => {
    this.state[prop] = newValue
    this.setState({ ...this.state });
  };

  handleInputChange = (prop, event) => {
    this.state[prop] = event.target.value === '' ? '' : Number(event.target.value)
    this.setState({ ...this.state });
  };

  radioHandleChange(prop, event) {
    this.state[prop] = event.target.value
    this.setState({ ...this.state });
  }

  handleBlur = (prop, min, max) => {
    if (this.state[prop] < min) {
      this.state[prop] = min
      this.setState({ ...this.state });
    } else if (this.state[prop] > max) {
      this.state[prop] = max
      this.setState({ ...this.state });
    }
  }

  handleToggle = value => () => {
    const { checked } = this.state
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    this.setState({ ...this.state, checked: newChecked });
  };

  render() {
    const contents = [
      { id: 1, title: 'お名前', prop: 'name', ctrl: { type: 0 } }
      , { id: 2, title: '性別', prop: 'gender', ctrl: { type: 2, kouho: [{ id: 1, label: '男性', value: 'male' }, { id: 2, label: '女性', value: 'female' }] } }
      , { id: 3, title: '年齢', prop: 'nenrei', ctrl: { type: 5, tani: '歳', min: 10, max: 120 } }
      , { id: 4, title: '身長', prop: 'shinchou', ctrl: { type: 4, tani: 'cm', min: 110, max: 220, step: 2 } }
      , { id: 5, title: '体重', prop: 'taijyu', ctrl: { type: 4, tani: 'Kg', min: 30, max: 200, step: .5 } }
      , { id: 6, title: '収縮期血圧', prop: 'ketsuatsu', ctrl: { type: 2, kouho: [{ id: 1, label: '〜129', value: '0' }, { id: 2, label: '130〜139', value: '1' }, { id: 3, label: '140〜', value: '2' }] } }
      , { id: 7, title: '高血圧', prop: 'kouketsuatsu', ctrl: { type: 2, kouho: [{ id: 1, label: 'なし', value: '0' }, { id: 2, label: 'あり', value: '1' }] } }
      , { id: 8, title: 'HbA1c', prop: 'hba1c', ctrl: { type: 2, kouho: [{ id: 1, label: '〜5.5', value: '0' }, { id: 2, label: '5.5〜6.4', value: '1' }, { id: 3, label: '6.5〜', value: '2' }] } }
      , { id: 9, title: 'HDLコレステロール', prop: 'hdl', ctrl: { type: 2, kouho: [{ id: 1, label: '〜39', value: '0' }, { id: 2, label: '40〜', value: '1' }] } }
      , { id: 10, title: 'LDLコレステロール', prop: 'ldl', ctrl: { type: 2, kouho: [{ id: 1, label: '〜119', value: '0' }, { id: 2, label: '120〜139', value: '1' }, { id: 2, label: '140〜', value: '2' }] } }
      , { id: 11, title: '糖尿病家族歴', prop: 'tounyoubyou', ctrl: { type: 2, kouho: [{ id: 1, label: 'なし', value: '0' }, { id: 2, label: 'あり', value: '1' }] } }
      , { id: 12, title: '１日の喫煙本数', prop: 'tabako', ctrl: { type: 2, kouho: [{ id: 1, label: '吸わない', value: '0' }, { id: 2, label: '1～9本', value: '1' }, { id: 3, label: '10本以上', value: '2' }] } }
      , { id: 13, title: '運動習慣', prop: 'sports', ctrl: { type: 2, kouho: [{ id: 1, label: 'なし', value: '0' }, { id: 2, label: 'あり', value: '1' }] } }
    ]
    const { value, checked, radioValue } = this.state

    return (
      <div className={this.props.classes.todoListRoot}>
        <Paper className={this.props.classes.todoListContent} style={{ display: this.state.kekka ? 'inherit' : 'none' }}>
          <Typography variant="h5" className={this.props.classes.title}>検査結果</Typography>  {/* classNameを追加 */}
        </Paper>
        <Paper className={this.props.classes.todoListContent}>
          <Typography variant="h5" className={this.props.classes.title}>検査項目</Typography>  {/* classNameを追加 */}
          <List className={this.props.classes.root}>
            {contents.map(data => {
              const labelId = `checkbox-list-label-${data.type}`;

              let description;
              if (data.ctrl.type === 0) {
                description = <Input className={this.props.classes.rootz} />
              }
              else if (data.ctrl.type === 1) {
                description =
                  <ListItemSecondaryAction className={this.props.classes.rootz}>
                    <IconButton edge="end" aria-label="comments">
                      <CommentIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
              }
              else if (data.ctrl.type === 2) {
                description =
                  <RadioGroup aria-label="gender" name="gender2" value={this.state[data.prop]} className={this.props.classes.rootz}
                    onChange={this.radioHandleChange.bind(this, data.prop)}
                  >
                    <div>
                      {data.ctrl.kouho.map(ss => {
                        return (
                          <FormControlLabel
                            value={ss.value}
                            control={<Radio color="primary" />}
                            label={ss.label}
                            labelPlacement="end"
                          />
                        )
                      })}
                    </div>
                  </RadioGroup>
              }
              else if (data.ctrl.type === 3) {
                description =
                  <div className={this.props.classes.rootz}>
                    <Grid container spacing={2} alignItems="center">
                      <Grid item>
                        <Input
                          className={this.props.classes.input}
                          value={this.state[data.prop]}
                          margin="dense"
                          onChange={this.handleInputChange.bind(this, data.prop)}
                          onBlur={this.handleBlur.bind(this, data.prop, data.ctrl.min, data.ctrl.max)}
                          inputProps={{
                            step: data.ctrl.step,
                            min: data.ctrl.min,
                            max: data.ctrl.max,
                            type: 'number',
                            'aria-labelledby': 'input-slider',
                          }}
                        />
                      </Grid>
                      <Grid item xs>
                        <Slider max={data.ctrl.max} min={data.ctrl.min}
                          value={typeof this.state[data.prop] === 'number' ? this.state[data.prop] : data.ctrl.min}
                          onChange={this.handleSliderChange.bind(this, data.prop)}
                          aria-labelledby="input-slider"
                        />
                      </Grid>
                    </Grid>
                  </div>
              }
              else if (data.ctrl.type === 4) {
                description =
                  <div className={this.props.classes.rootz}>
                    <Grid container spacing={2} alignItems="center">
                      <Grid item>
                        <Input
                          //                          id="adornment-weight"
                          id={data.id}
                          value={this.state[data.prop]}
                          endAdornment={<InputAdornment position="end">{data.ctrl.tani}</InputAdornment>}
                          aria-describedby="weight-helper-text"
                          onChange={this.handleInputChange.bind(this, data.prop)}
                          onBlur={this.handleBlur.bind(this, data.prop, data.ctrl.min, data.ctrl.max)}
                          inputProps={{
                            step: data.ctrl.step,
                            min: data.ctrl.min,
                            max: data.ctrl.max,
                            type: 'number',
                            'aria-labelledby': 'input-slider',
                          }}
                        />
                      </Grid>
                      <Grid item xs>
                        <Slider max={data.ctrl.max} min={data.ctrl.min}
                          value={typeof this.state[data.prop] === 'number' ? this.state[data.prop] : data.ctrl.min}
                          onChange={this.handleSliderChange.bind(this, data.prop)}
                          aria-labelledby="input-slider"
                        />
                      </Grid>
                    </Grid>
                  </div>
              }
              else if (data.ctrl.type === 5) {
                description =
                  <div className={this.props.classes.rootz}>
                    <Grid container spacing={2} alignItems="center">
                      <Grid item xs>
                        <TextField
                          id="outlined-adornment-weight"
                          //                    className={clsx(classes.margin, classes.textField)}
                          variant="outlined"
                          //                    label="Weight"
                          value={this.state[data.prop]}
                          onChange={this.handleInputChange.bind(this, data.prop)}
                          onBlur={this.handleBlur.bind(this, data.prop, data.ctrl.min, data.ctrl.max)}
                          InputProps={{
                            endAdornment: <InputAdornment position="end">{data.ctrl.tani}</InputAdornment>,
                          }}
                        />
                      </Grid>
                      <Grid item xs>
                        <Slider max={data.ctrl.max} min={data.ctrl.min}
                          value={typeof this.state[data.prop] === 'number' ? this.state[data.prop] : data.ctrl.min}
                          onChange={this.handleSliderChange.bind(this, data.prop)}
                          aria-labelledby="input-slider"
                        />
                      </Grid>
                    </Grid>
                  </div>
              }
              return (
                //          <ListItem key={type} role={undefined} dense button onClick={handleToggle(type)}>
                <ListItem key={data.id} role={undefined} dense >
                  <ListItemIcon>
                    <Checkbox
                      edge="start"
                      checked={checked.indexOf(data.id) !== -1}
                      tabIndex={-1}
                      disableRipple
                      inputProps={{ 'aria-labelledby': labelId }}
                    />
                  </ListItemIcon>
                  <ListItemText id={labelId} primary={`${data.title}`} />
                  {description}
                </ListItem>
              );
            })}
          </List>
          <Button variant="contained" color="primary" onClick={this.onClick}>診断</Button>
        </Paper>
      </div>
    );
  }
}

//export default App;
export default withStyles(styles)(App)